import type { Action } from "@ali/4ever-cangjie";

export const HIGHLIGHT_SET = Symbol('HIGHLIGHT_SET');

export interface SetHighlightAction {
  type: typeof HIGHLIGHT_SET,
  payload: {
    highlight?: string;
  }
}

export function setHighlight(highlight): SetHighlightAction {
  return {
    type: HIGHLIGHT_SET,
    payload: {
      highlight,
    },
  };
}

export function isHighlight(action: Action): action is SetHighlightAction {
  return action?.type === HIGHLIGHT_SET;
}

export const actions = {
  setHighlight,
}

export const isActions = {
  isHighlight
}
